import { Gain, Synth } from 'tone';
const triangleSynth = new Synth();
triangleSynth.oscillator.type = 'triangle';
const sawSynth = new Synth();
sawSynth.oscillator.type = 'sawtooth';
const fmSynth = new Synth();
sawSynth.oscillator.type = 'fmsine';
const gain = new Gain(0.3);
gain.toDestination();
[triangleSynth, sawSynth, fmSynth].forEach((synth) => synth.toDestination());
const instruments = [
    { instrument: triangleSynth, note: 'G5' },
    { instrument: sawSynth, note: 'E4' },
    { instrument: fmSynth, note: 'C3' },
];
export { instruments };
