<script lang="ts">
	import * as Tone from 'tone';
	import { onMount } from 'svelte';
	import { sekwense, solution, instruments } from '../stores';
	import BeatPanel from '../components/BeatPanel.svelte';
	import InstrumentPanel from '../components/InstrumentPanel.svelte';
	import SekwensePanel from '../components/SekwensePanel.svelte';

	let index = 0;
	let started = false;
	let playing = 'solution';
	let solved = false;

	const start = (toPlay = 'solution') => {
		console.log('Starting transport');
		playing = toPlay;
		Tone.Transport.start();
		started = true;
	}

	const stop = () => {
		console.log('Stopping transport');
		Tone.Transport.stop();
		started = false;
	}

	const onRepeat = (time) => {
		let step = index % 8;
		for (let i = 0; i < $sekwense.length; i += 1) {
			const toCheck = playing === 'solution' ? $solution[i][step] : $sekwense[i][step];
			if (toCheck) {
				const { instrument, note } = $instruments[i];
				instrument.triggerAttackRelease(note, '8n', time)
			}
		}

		index++;
	};

	const check = () => {
		let same = true;

		for (let i = 0; i < $solution.length; i += 1) {
			for (let n = 0; n < $solution[i].length; n += 1) {
				if ($sekwense[i][n] !== $solution[i][n]) {
					same = false;
					return;
				}
			}
		}

		solved = same;
	}

	onMount(() => {
		Tone.Transport.scheduleRepeat(onRepeat, '8n');
  });
</script>

<style>
	section {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: 1rem;
	}

	section > button + button {
		margin-left: 0.5rem;
	}

	button {
		border: none;
		border-radius: 3px;
		background: #222;
		color: #eee;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 0.5rem 1rem;
		text-transform: uppercase;
		font-size: 1.1rem;
	}
</style>

{#if $solution && $solution.length}
	<section>
		{#if started && playing === 'solution'}	
			<button on:click={stop}>Stop</button>
		{:else}
			<button on:click={() => start('solution')} disabled={started && playing !== 'solution'}>Solve Me</button>
		{/if}
	</section>
	<div style="display: flex; flex-direction: column">
		<div style="display: flex; justify-content: flex-end;">
			<BeatPanel index={index} />
		</div>
		<section>
			<InstrumentPanel />
			<SekwensePanel />
		</section>
	</div>
	<section>
		{#if started && playing === 'sekwense'}	
			<button on:click={stop}>Stop</button>
		{:else}
			<button on:click={() => start('sekwense')} disabled={started && playing !== 'sekwense'}>Play Me</button>
		{/if}
		<button on:click={check}>Check Me</button>
		{#if solved}
			<b>Solved.</b>
		{/if}
	</section>
{/if}
