<script lang="ts">
	import { sekwense } from '../stores';

	const changeNote = (row, col) => {
		console.log(`Changing Note [${row}][${col}]`);
		const current = $sekwense[row][col];
		$sekwense[row][col] = current ? 0 : 1;
		$sekwense[row] = [...$sekwense[row]];
	}
</script>

<style>
  section {
    display: flex;
    flex-direction: column;
  }

  div {
    display: flex;
  }

	span {
		display: block;
		width: 3rem;
		height: 3rem;
		background: #bbb;
		margin-bottom: 0.5rem;
	}

	span + span {
		margin-left: 0.5rem;
	}

	span.enabled {
		background: #c33;
	}
</style>

<section>
  {#each $sekwense as track, row}
    <div>
      {#each track as note, col}
        <span class={note ? 'enabled' : ''} on:click={() => changeNote(row, col)} />
      {/each}
    </div>
  {/each}
</section>
